.login {
  height: 100vh;
  width: 100vw;
}

.titlebar-login {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 8vh;
  background-color: #225311;
  color: #e2e97e;
  font-family: "Montserrat", sans-serif;
  font-size: 6vh;
}

.body-login {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 92vh;
  width: 100vw;
  background-color: rgb(211, 211, 211);
  background-image: radial-gradient(#225311 20%, transparent 20%),
    radial-gradient(#78a567 20%, transparent 20%);
  background-position: 0 0, 7.5vh 7.5vh;
  background-size: 15vh 15vh;
}

.wrapper-button-login {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #225311;
  height: 25vh;
  width: 40vw;
  border-radius: 5vh;
  transition: 0.5s;
}

.wrapper2-login {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #225311;
  height: 25vh;
  width: 40vw;
  border-radius: 5vh;
  transition: 0.5s;
  opacity: 0;
}

.button-login {
  all: unset;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 8vh;
  width: 15vw;
  margin-right: 2vw;
  margin-left: 2vw;
  border-radius: 2vh;
  background-color: #225311;
  color: #e2e97e;
  font-family: "Montserrat", sans-serif;
  font-size: 3vh;
  transition: 0.3s;
  border: #225311 0.3vh solid;
  margin: 1vh;
}

.button-login:hover {
  background-color: #17380c;
  border: rgb(0, 0, 0) 0.3vh solid;
  background-image: radial-gradient(black 20%, transparent 20%),
    radial-gradient(black 20%, transparent 20%);
  background-position: 0 0, 0.8vh 0.8vh;
  background-size: 1vh 1vh;
}

/* This is to control what to see ======================================*/
.screen1-login {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  z-index: inherit;
  transition: 1s;
}

.screen2-login {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  opacity: 0;
  z-index: -1;
  transition: 1s;
}

.screen3-login {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  opacity: 0;
  z-index: -1;
  transition: 1s;
}

/* screen 2 stuff ======================================*/

.wrapper-screen2-login {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

/* Screen 3 stuff ======================================*/

.disclaimer-login {
  color: #e2e97e;
  font-family: "Montserrat", sans-serif;
  font-size: 1vh;
  width: 40vw;
  text-align: center;
  margin-top: 2vh;
}

/* Screen 3 stuff ======================================*/
@media only screen and (orientation: portrait) {
  .titlebar-login {
    height: 10vh;
  }

  .body-login {
    height: 90vh;
  }

  .wrapper-button-login {
    height: 40vh;
    width: 70vw;
  }

  .screen1-login {
    flex-direction: column;
  }

  .button-login {
    width: 55vw;
    font-size: 7vh;
    padding-top: 3vh;
    padding-bottom: 3vh;
  }
}

